          "Фрагмент 1": "При подтверждении соответствия референтной методики метрологическим требованиям к измерениям уполномоченной организацией проводится оценивание правильности (обоснованности) метода;средств измерений; тандартных образцов; вспомогательных и других технических средств;  установления последовательности и содержания операций при отборе и подготовке проб, подготовке к измерениям и выполнении этих измерений;  обработки промежуточных результатов измерений и вычислений окончательных результатов измерений;  модели измерений;  программы и объема экспериментальных исследований, включая стадии (методы) отбора и подготовки проб;  выполненных теоретических исследований;  выбора и оценки условий проведения измерений;  выбора способов и средств обработки результатов измерений, включая программное обеспечение;  выбора показателей точности измерений и установления их значений;  выбора процедур обеспечения установленных в результате аттестации показателей точности измерений (в том числе методов обеспечения прослеживаемости результата измерений к государственным первичным эталонам единиц величин, а также способов контроля показателей точности) и достоверности принимаемых решений (при необходимости). При подтверждении возможности использования референтной методики для оценки правильности измеренных значений, полученных с помощью других методик (методов) измерений величин того же рода, для калибровки средств измерений или для определения характеристик стандартных образцов уполномоченной организацией проводится проверка наличия материалов теоретических и (или) экспериментальных исследований в соответствии с подпунктами \"б\" - \"ж\" пункта 6 настоящего Порядка.",
